:root {
    --nav-height: 5rem;
    --side-gap: 8rem;
    --border-radius: 0.5em;

    --clr-primary-100: #e3e3eb;
    --clr-primary-200: #b6b6ce;
    --clr-primary-300: #8585ad;
    --clr-primary-400: #5a5a87;
    --clr-primary-500: #404060;
    --clr-primary-900: #0d0d13;

    --clr-secondary-100: #f1e6ea;
    --clr-secondary-200: #d6b4c1;
    --clr-secondary-300: #ba8298;
    --clr-secondary-400: #995470;
    --clr-secondary-500: #703e52;

    --clr-accent-500: #cc5a71;

    --clr-highlight-inv-500: #f0f757;

    --clr-highlight-500: #c89b7b;
}

@media only screen and (max-width: 768px) {
    :root {
        --side-gap: 1.5em;
    }
}

body {
    margin: 0;
    overflow: hidden;
    height: 100vh;

    /* font */
    font-family: "Lato", sans-serif;
    color: var(--clr-primary-900);
    /* line-height: 1.5; */
    font-weight: 500;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    /* bg */
    /* background: var(--clr-primary-100); */
}

p {
    text-indent: 2em;
    line-height: 1.8;
}

#root {
    margin: 0;
    padding: 0;
    height: 100vh;
}

.my-align {
    text-align: center;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

a:link {
    text-decoration: none;
}

h2 {
    display: block;
    padding-bottom: 1.5rem;
    position: relative;
    margin-bottom: 2rem;
    letter-spacing: 0.25rem;
    border: 4px solid;
    border-image-slice: 1;
    border-image-source: linear-gradient(to right, #C43F59, transparent);
    border-top: transparent;
    border-right: transparent;
    border-left: transparent;
}

h2::after {
    content: " ";
    /* border-bottom: 0.3rem solid var(--clr-secondary-500); */
    border-bottom-width: 0.2rem;
    border-bottom-style: solid;
    border-image: linear-gradient(to bottom, red, rgba(0, 0, 0, 0)) 1 100%;
    width: 40%;
    bottom: 0;
    left: 0;
    position: absolute;
}

h2::before {
    content: " ";
    background-color: var(--clr-secondary-400);
}

@media only screen and (max-width: 768px) {
    .card {
        --card-width: 100%;
    }
}
@media only screen and (min-width: 769px) {
    .card {
        --card-width: calc(var(--card-size) + 7rem);
        --card-height: var(--card-size);
    }
}
.card {
    --card-size: 15rem;

    width: var(--card-width);
    height: var(--card-height);
    border-radius: var(--border-radius);
    border: 1px solid darkgray;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    font-size: 0.8rem;
}

.grid-container {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    height: 100%;
    width: 100%;
}

* {
    box-sizing: border-box;
    /* outline: 0.001rem solid yellowgreen; */
}
